import OpenAI from "openai";

const openai = new OpenAI({
    apiKey: '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', //  OpenAI API 
    dangerouslyAllowBrowser: true 
  });

async function create_assistant() {
    const myAssistant = await openai.beta.assistants.retrieve(
        "asst_d7Lx7PplsMyr0TO6DVAMA5kh"
    );
    return myAssistant
}

async function create_thread() {
    const emptyThread = await openai.beta.threads.create();
    return emptyThread
}
  

const ASSISTANT = await create_assistant()
const THREAD = await create_thread()

async function gpt(text) {
    const message = await openai.beta.threads.messages.create(
        THREAD.id,
        { role: "user", content: text }
        );
    let run = await openai.beta.threads.runs.create(
        THREAD.id,
        { assistant_id: ASSISTANT.id }
        );

    async function waitOnRun(run, thread) {
        //  Promise 
        const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));
    
        //  run 
        while (run.status === "queued" || run.status === "in_progress") {
            //  API  run 
            run = await openai.beta.threads.runs.retrieve(
                thread.id,
                run.id
            );
    
            // 
            await delay(500); //  0.5 
        }
    
        //  run 
        return run;
    }

    run = await waitOnRun(run, THREAD);

    const messages = await openai.beta.threads.messages.list(
        THREAD.id,
        {limit:10, order:"asc", after:message.id}
        );
    
    let response = null;
    for (const msg of messages.data) {
        if (msg.role === "assistant") {
            response = msg.content[0].text.value; // 
            break; //  'assistant' 
        }
    }
    return response;
}

export default gpt;